import Box from "@mui/material/Box";
import Progress from "@mui/material/CircularProgress";

import { memo } from "react";
import { progressStyle, loaderContainerStyle } from "styles/sxStyles";

/**
 * creates memoised spinner that indicates loading state
 * @returns spinner component
 */
const Loader = memo(() => {
    return (
        <Box sx={{ ...loaderContainerStyle }}>
            <Progress sx={{ ...progressStyle }} thickness={5} size={100} />
        </Box>
    );
});

export default Loader;
